body {
  justify-content: flex-end;
  padding: 20px;
  font-family: Raleway, sans-serif;
  display: flex;
}

body:before {
  content: "";
  z-index: -1;
  background-image: url("../javea.9c860eb6.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
}

.container {
  flex-direction: column;
  width: 440px;
  margin-right: 160px;
  display: flex;
}

.title {
  color: #fff;
  text-shadow: 1px 1px 1px #000, 0 0 25px #00f, 0 0 7px #fa6ff5;
  letter-spacing: .02em;
  align-self: center;
  margin-bottom: 10px;
  font-family: Raleway;
  font-size: 42px;
  font-weight: 500;
  line-height: 1.2;
}

.input {
  color: #3e62be;
  border: none;
  border-radius: 8px;
  height: 40px;
  margin-bottom: 8px;
  padding-left: 8px;
  box-shadow: 1px 1px #000, 0 0 5px #00f, 0 0 8px #69cbf6af;
}

::placeholder {
  color: #445ea0;
  opacity: 1;
}

.input:focus-visible {
  outline: #aa9cf9;
  box-shadow: .2px .2px .2px #000000d4, 0 0 3px #3c0fdb, 0 0 20px #0000ffd8, inset 5px 5px 10px #fcb3faee;
}

.buttons {
  gap: 2px;
  margin: 0 auto;
  display: inline-flex;
}

.button {
  color: #fff;
  text-shadow: 1px 1px 1px #000, 0 0 25px #00f, 0 0 7px #fa6ff5;
  letter-spacing: .03em;
  background-color: #ffffff8b;
  border: none;
  border-radius: 8px;
  width: 199px;
  height: 40px;
  font-family: Raleway;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.2;
  box-shadow: 1px 1px #000, 0 0 5px #00f, 0 0 8px #69cbf6af;
}

.button:hover, .button:focus {
  background: #f8bdf6;
  box-shadow: .2px .2px .2px #000000e8, 0 0 20px #00fe, 0 0 3px #00008bf1;
}

